// ========== sidebar ==========

.sidebar {
    display: none;

    @include mq(tablet) {
        display: flex;
        align-items: flex-end;
        z-index: 10;

        width: 90px;
        padding-left: 25px;
        padding-bottom: 40px;
    }

    & .socials {
        display: flex;
        align-items: center;

        padding-top: 36px;
        padding-bottom: 37px;

        transform: rotate(-90deg) translateY(100%);
        transform-origin: left bottom;
    }
}

// ========== title group ==========

.hero__group {
    display: inline-block;
    flex-grow: 1;
    z-index: 10;
    margin-top: 208px;
    margin-right: 143px;
    margin-bottom: 183px;
    margin-left: 31px;

    @include mq(tablet) {
        margin-top: 272px;
        margin-right: 134px;
        margin-bottom: 245px;
        margin-left: 120px;
    }

    @include mq(desktop) {
        margin-right: 134px;
        margin-bottom: 206px;
        margin-left: 100px;
    }

    & .subtitle {
        margin-bottom: 0;
        max-width: 306px;

        animation: fade-bottom-to-top 2s ease;

        @include mq(tablet) {
            max-width: 380px;
        }
    }
}

.main-tittle {
    margin-bottom: 20px;

    font-family: var(--font-family-title);
    font-weight: 700;
    font-size: 48px;
    line-height: 1.17;
    letter-spacing: 0.05em;

    color: var(--color-text-white);

    animation: fade-left-to-right 1s ease-out;

    @include mq(desktop) {
        font-size: 72px;
        line-height: 1.18;
    }
}